/* @flow */

import Rx from 'rx';

var literal = Rx.Observable.of;

export
default

function emitMembers(memberNodes: Rx.Observable, nodeMapper: (node: any) => Rx.Observable): Rx.Observable {
    return memberNodes.concatMap(
            subnode => literal('    ') /* indent */
            .concat(
                nodeMapper(subnode),
                literal('\n')
            )
        );
}